import React, {useEffect, useRef, useState} from 'react'
import { SectionWrapper } from './SectionWrapper'
import { MyScrollAnimation } from './MyScrollAnimation'

export const SectionLayout = (props) => {
  const { id, wrap = true, Header, Footer, children, className, sectionClassName, customWrap = false } = props
    let elem = useRef(null)
    const [isBottom, setIsBottom] = useState(false)

    const handleScroll = (e) => {
        const window = e.currentTarget;
        console.log(window.scrollY)

        if(elem.current.getBoundingClientRect().top <= 0) {
            setIsBottom(true);
        } else {
            setIsBottom(false)
        }
    }

    useEffect(() => {
        window.addEventListener('scroll', handleScroll)

        return () => {
            window.removeEventListener('scroll', handleScroll)
        }
    }, [])

  return (
    <section ref={elem} id={id} onScroll={handleScroll} className={`project-section ${sectionClassName ? sectionClassName : ''}`}>
      <MyScrollAnimation animationName="fadeIn" offset={id.includes('-main') ? 0 : 300}>
        <div className={`project-section__container ${sectionClassName ? sectionClassName+ '__container': ''}`}>
          {Header && <Header />}
          {children && (
            customWrap ? (
              children
            ) : wrap ? (
              <SectionWrapper className={`${className ? className : ''} ${isBottom ? 'active' : ''}`}>
                {children}
              </SectionWrapper>
            ) : (
              children.map((Child, index) => (
                <SectionWrapper key={index} className={Child.props.className}>
                  {Child}
                </SectionWrapper>
              ))
            )
          )}
          {Footer && <Footer />}
        </div>
      </MyScrollAnimation>
    </section>
  )
}
